
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import api from '@/api/index'
export default {
data () {
return {
isVoice: true,
textValue: '',
networkType: '',
config: {}
}
},
computed: {
wx: function () {
let wx = this.$wechat
this.config['debug'] = false
this.config['jsApiList'] = [
'scanQRCode'
]
wx.config(this.config)
wx.ready(function () {
})
return wx
}
},
components: {
Grid,
GridItem,
GroupTitle,
XHeader,
Flexbox,
FlexboxItem,
XButton,
Group,
XTextarea
},
methods: {
cancel () {
this.$router.go(-1)
},
scan () {
let _this = this
this.wx.scanQRCode({
desc: 'scanQRCode desc',
needResult: 1, // 默认为0，扫描结果由企业微信处理，1则直接返回扫描结果，
scanType: ['qrCode', 'barCode'], // 可以指定扫二维码还是一维码，默认二者都有
success: function (res) {

var json = {}
try {
json = JSON.parse(res.resultStr)
json = json.zcdm
if (json == null || json === '') {
json = res.resultStr
}
} catch (error) {
json = res.resultStr
}
if (json.length > 35) {
_this.$vux.alert.show({content: '非法信息'})
return
}
api.post('/wx/zczt/isExist.do', {
dm: json
}).then((response) => {
if (response == null) {
_this.$router.push({name: 'Zcdj', query: {zcdm: json}})
} else {
_this.$vux.alert.show({
title: '提示',
content: '资产已登记！'
})
_this.$store.dispatch('zcxqShowBack')
_this.$router.push({name: 'ZcxqNav', query: {zcid: response.id}})
}
})
.catch((response) => {
console.log(response)
})
},
error: function (res) {
if (res.errMsg.indexOf('function_not_exist') > 0) {
_this.$vux.alert.show({
title: '提示',
content: '版本过低请升级！'
})
}
}
})
}
},
mounted: function () {
console.log('加载')
api.post('/wx/common/getWXConfig.do', {
url: location.href,
appId: this.$route.query.appId
}).then((response) => {
this.config = response
})
.catch((response) => {
console.log(response)
})
}
}



