
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import api from '@/api/index'
export default {
components: {
Group,
XHeader,
XButton,
PopupPicker
},
methods: {
tel (v) {
return 'tel:' + v
},
onChange () {
let _this = this
_this.deptId = _this.bmValue[0]
var temp = _this.bmValue
_this.page = 1
var postData = ''
if (temp[0].indexOf('null') !== -1) {
postData = ''
} else if (temp[1].indexOf('null') !== -1) {
postData = temp[0]
} else if (temp[2].indexOf('null') !== -1) {
postData = temp[1]
} else {
postData = temp[2]
}
_this.deptId = postData
api.post('/wx/myconcat/getChuWXUserListByDept.do', {
deptId: postData,
rows: _this.rows,
page: _this.page
}).then((response) => {
_this.page++
_this.contactsList = response
})
},
jiazai () {
let _this = this
api.post('/wx/myconcat/getChuWXUserListByDept.do', {
deptId: _this.deptId,
rows: _this.rows,
page: _this.page
}).then((response) => {
_this.page++
_this.contactsList = _this.contactsList.concat(response)
})
}
},
data () {
return {
contactsList: [],
page: 1,
rows: 10,
bmCol: 3,
deptId: '',
bmList: [],
bmValue: []
}
},
mounted: function () {
let _this = this
api.post('/wx/myconcat/getChuWXUserListByDept.do', {
deptId: _this.deptId,
rows: _this.rows,
page: _this.page
}).then((response) => {
_this.page++
_this.contactsList = response
})
api.post('/wx/myconcat/getWXDeptPickWithAll.do', {}).then((response) => {
_this.bmList = response.res

})
},
beforeRouteEnter (to, from, next) {
next(vm => {
vm.$store.dispatch('hideMainTabbar')
})
},
beforeRouteLeave (to, from, next) {
this.$store.dispatch('showMainTabbar')
next()
}
}



