
import api from '@/api/index'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import Vue from 'vue'
Vue.use(ToastPlugin)
export default {
components: {
PopupPicker,
Group,
Picker,
ToastPlugin,
XButton,
Flexbox,
FlexboxItem,
Cell,
XHeader
},
methods: {
onChange () {
let _this = this
_this.cfddCopy = _this.cfddValue
if (_this.cfddValue[2].indexOf('null') >= 0) {
var temp = ['', '', '']
_this.cfddValue = temp
} else {
api.post('/wx/zczt/getCFDDByFjid.do', {
fjId: _this.cfddCopy[2]
}).then((response) => {
if (response != null) {
var temp = ['', '', response]
_this.cfddValue = temp
} else {
this.$vux.toast.show({
type: 'warn',
text: '失败'
})
}
})
.catch((response) => {
this.$vux.toast.show({
type: 'warn',
text: '失败'
})
})
}
},
cancel () {
this.$router.go(-1)
},
allocateZC () {
console.log(this.glbmValue)
if (this.glbmValue.length === 0) {
this.$vux.toast.show({
type: 'warn',
text: '请选择资产管理部门'
})
return
}
if (this.cfddValue.length === 0) {
this.cfddValue = ['null1', 'null2', 'null3']
}
api.post('/wx/zczt/allocateZC.do', {
zcId: this.$route.query.zcId,
zcglId: this.glbmValue[0],
cfdd: this.cfddCopy[2]
}).then((response) => {
if (response === 'success') {
this.$vux.toast.show({
text: '成功'
})
this.$router.go(-1)
} else {
this.$vux.toast.show({
type: 'warn',
text: '失败'
})
}
})
.catch((response) => {
this.$vux.toast.show({
type: 'warn',
text: '失败'
})
})
}
},
data () {
return {
glbmList: [],
glbmValue: [],
cfddLidt: [],
cfddValue: [],
cfddCopy: [],
title1: '资产管理部门',
title2: '资产存放地点'
}
},
mounted: function () {
console.log('加载')
api.get('/wx/zczt/getZCGLPicker.do').then((response) => {
this.glbmList = response
})
.catch((response) => {
console.log(response)
})
api.get('/wx/zczt/getFJPicker.do').then((response) => {
this.cfddLidt = response
})
.catch((response) => {
console.log(response)
})
},
beforeRouteEnter (to, from, next) {
next(vm => {
vm.$store.dispatch('hideMainTabbar')
})
},
beforeRouteLeave (to, from, next) {
this.$store.dispatch('showMainTabbar')
next()
}
}



