
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import api from '@/api/index'
export default {
components: {
Popup,
Group,
XHeader,
XButton
},
directives: {
TransferDom
},
computed: {
wx: function () {
let wx = this.$wechat
this.config['debug'] = true
this.config['jsApiList'] = [
'playVoice',
'pauseVoice',
'stopVoice',
'onVoicePlayEnd',
'downloadVoice',
'previewImage',
'downloadImage'
]
wx.config(this.config)
return wx
}
},
methods: {
getMore () {
let _this = this
api.post('/wx/action/getVZTList.do', {
rows: _this.rows,
page: _this.page
}).then((response) => {
_this.page++
_this.ztData = _this.ztData.concat(response)
})
},
showImg (item) {
let _this = this
let _wx = _this.wx
_this.showMediaDetail(_this, _wx, item)
},
showMediaDetail (_this, _wx, item) {
let mediaType = item.mediaType
let ztid = item.ztid
api.post('/wx/xq/zcxq/zcxzInfo.do', {mediaType: mediaType, ztid: ztid}).then(function (res) {
if (res) {
if (mediaType === 'image') {
_wx.previewImage({
current: res[0], // 当前显示图片的http链接
urls: res // 需要预览的图片http链接列表
})
} else if (mediaType === 'voice') {
_this.showVoice(res, item.jlsj)
}
}
})
},
showVoice (voiceUrl, jlsj) {
this.voiceUrl = voiceUrl[0]
if (jlsj) this.voiceTile = api.getFmtDate(jlsj, 'YYYY年MM月DD日')
this.show = !this.show
},
stopPlay () {
let audio = document.getElementById('ztVoice')
if (audio) audio.pause()
}
},
beforeRouteEnter (to, from, next) {
next(vm => {
vm.$store.dispatch('hideMainTabbar')
})
},
beforeRouteLeave (to, from, next) {
this.$store.dispatch('showMainTabbar')
next()
},
data () {
return {
show: false,
voiceUrl: '',
voiceTile: '',
config: {},
page: 1,
rows: 10,
ztData: []
}
},
mounted: function () {
console.log('加载')
api.post('/wx/common/getWXConfig.do', {
url: location.href
}).then((response) => {
this.config = response
})
api.post('/wx/action/getVZTList.do', {
rows: this.rows,
page: this.page
}).then((response) => {
this.ztData = response
this.page += 1
})
}
}



