
import api from '@/api/index'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import Vue from 'vue'
Vue.use(ToastPlugin)
export default {
components: {
Group,
XHeader,
XTextarea,
XInput,
ToastPlugin,
XButton,
Flexbox,
FlexboxItem,
Cell,
PopupPicker
},
methods: {
cancel () {
this.$router.go(-1)
},
reassignZC () {
if (this.zcsyr.length === 0 || this.zcsyr === null) {
this.$vux.toast.show({
type: 'warn',
text: '请选择一个资产使用人'
})
return
}
api.post('/wx/zczt/reassignZC.do', {
zcId: this.$route.query.zcId,
syr: this.zcsyr[1],
remark: this.remark
}).then((response) => {
this.$router.go(-1)
if (response === 'success') {
this.$vux.toast.show({
text: '资产重新分配成功'
})
} else {
this.$vux.toast.show({
type: 'warn',
text: '资产分配失败'
})
}
})
.catch((response) => {
this.$vux.toast.show({
type: 'warn',
text: '失败'
})
})
}
},
mounted: function () {
api.post('/wx/zczt/getOldZCSYR.do', { zcId: this.$route.query.zcId }).then((response) => {
this.oldSyr = response
})
.catch((response) => {
console.log(response)
})
api.get('/wx/zczt/getUserPicker.do', { zcId: this.$route.query.zcId }).then((response) => {
this.newZcsyr = response
})
.catch((response) => {
console.log(response)
})
},
data () {
return {
oldSyr: '',
newZcsyr: [],
title: '新的资产使用人',
zcsyr: [],
remark: ''
}
},
beforeRouteEnter (to, from, next) {
next(vm => {
vm.$store.dispatch('hideMainTabbar')
})
},
beforeRouteLeave (to, from, next) {
this.$store.dispatch('showMainTabbar')
next()
}
}



