
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'

import { mapGetters } from 'vuex'
export default {
components: {
Tabbar,
TabbarItem,
ViewBox
},
computed: {
...mapGetters([
'showMainTabbar',
'gzCountNo',
'getDirection'
]),
tagName () {
return this.$route.query.tagName
}
},
mounted () {
},
methods: {
toZc () {
this.$router.replace({name: 'NaviZc', query: {tagName: this.tagName}})
},
toGz () {
this.$router.replace({name: 'NaviGz', query: {tagName: this.tagName}})
},
toMy () {
this.$router.replace({name: 'NaviMy', query: {tagName: this.tagName}})
},
clearCount () {
}
}
}



