
import api from '@/api/index'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import Vue from 'vue'
Vue.use(ToastPlugin)
export default {
components: {
PopupPicker,
Group,
Picker,
ToastPlugin,
XButton,
Flexbox,
FlexboxItem,
Cell,
XHeader,
XTextarea
},
methods: {
cancel () {
this.$router.go(-1)
},
refuseXZSQ () {
if (this.remark.length === 0 || this.remark === null) {
this.$vux.toast.show({
type: 'warn',
text: '请输入拒绝原因'
})
}
api.post('/wx/zczt/refuseXZSQ.do', {
refuseRemark: this.remark,
zcId: this.$route.query.zcId
}).then((response) => {
this.$router.go(-1)
if (response === 'success') {
this.$vux.toast.show({
text: '以拒绝资产闲置申请'
})
} else {
this.$vux.toast.show({
type: 'warn',
text: '拒绝资产闲置申请失败'
})
}
})
.catch((response) => {
this.$vux.toast.show({
type: 'warn',
text: '失败'
})
})
}
},
data () {
return {
remark: ''
}
},
beforeRouteEnter (to, from, next) {
next(vm => {
vm.$store.dispatch('hideMainTabbar')
})
},
beforeRouteLeave (to, from, next) {
this.$store.dispatch('showMainTabbar')
next()
}
}



