
/* eslint-disable yoda */
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import api from '@/api/index'
export default {
data () {
return {
zcdm: '',
zcmc: '',
cost: 0,
num: 1,
xh: '',
ccbh: '',
gzsj: '',
zjnx: 3,
zclxList: [],
zclx: [],
isVoice: true,
value: 0,
textValue: '',
images: {
localId: [],
serverId: []
},
voice: {
localId: '',
serverId: ''
},
config: {}
}
},
computed: {
wx: function () {
let wx = this.$wechat
this.config['debug'] = false
this.config['jsApiList'] = [
'chooseImage',
'previewImage',
'uploadImage',
'downloadImage'
]
wx.config(this.config)
wx.ready(function () {
})
return wx
}
},
components: {
Grid,
GridItem,
Datetime,
PopupPicker,
Flexbox,
GroupTitle,
XHeader,
FlexboxItem,
XButton,
Group,
XInput
},
methods: {
cancel () {
this.$router.go(-1)
},
timeChange () {
console.log(this.cost)
console.log(typeof this.cost)
console.log(this.cost === '')
},
sumbitZcdj () {
let _this = this
let ids = _this.images.serverId
let data = ''
for (var i = 0; i < ids.length - 1; i++) {
data = data + ids[i] + '###'
}
data = data + ids[ids.length - 1]
if (ids.length === 0) {
this.$vux.toast.show({
type: 'warn',
text: '请选择图片'
})
return
}
if (_this.zcmc.length === 0) {
this.$vux.toast.show({
type: 'warn',
text: '请填写资产名称'
})
return
}
if (_this.zclx[1] == null) {
this.$vux.toast.show({
type: 'warn',
text: '请选择资产类型'
})
return
}
if (this.cost === '') {
this.$vux.toast.show({
type: 'warn',
text: '请正确填写单价(比如10.24)'
})
return
}
if (_this.num === '') {
this.$vux.toast.show({
type: 'warn',
text: '请正确填写数量(比如10)'
})
return
}
if (_this.gzsj === '') {
this.$vux.toast.show({
type: 'warn',
text: '请选择购置时间'
})
return
}
if (_this.zjnx === '') {
this.$vux.toast.show({
type: 'warn',
text: '请填写折旧年限'
})
return
}
api.post('/wx/zczt/sumbitZCDJ.do', {
imgId: data,
zcdm: _this.$route.query.zcdm,
zcmc: _this.zcmc,
zcLx: _this.zclx[1],
cost: _this.cost,
num: _this.num,
xh: _this.xh,
ccbh: _this.ccbh,
gzsj: _this.gzsj,
zjnx: _this.zjnx
}).then((response) => {
if (response === 'success') {
this.$vux.toast.show({
text: '成功'
})
this.$router.go(-1)
}
})
.catch((response) => {
_this.$vux.toast.show({
type: 'warn',
text: '失败'
})
})
},
chooseImage () {
let _this = this
_this.wx.chooseImage({
count: 1, // 默认9
isShowProgressTips: 1, // 默认为1，显示进度提示
success: function (res) {
_this.images.localId = res.localIds
_this.$vux.alert.show({content: '已选择 ' + res.localIds.length + ' 张图片'})
}
})
},
previewImage () {
let _this = this
_this.wx.previewImage({
current: _this.images.localId[0],
urls: _this.images.localId
})
},
uploadImage () {
let _this = this
if (_this.images.localId.length === 0) {
_this.$vux.alert.show({content: '请先选择图片'})
return
}
var i = 0
var length = _this.images.localId.length
_this.images.serverId = []
function upload () {
_this.wx.uploadImage({
localId: _this.images.localId[i],
success: function (res) {
i++
_this.images.serverId.push(res.serverId)
if (i < length) {
upload()
}
},
fail: function (res) {
alert(JSON.stringify(res))
}
})
}
upload()
}
},
mounted: function () {
this.zcdm = this.$route.query.zcdm
console.log(this.gzsj)
api.post('/wx/common/getWXConfig.do', {
url: location.href,
appId: this.$route.query.appId
}).then((response) => {
this.config = response
})
.catch((response) => {
console.log(response)
})
api.post('/wx/zczt/getZCLXPicker.do', {
}).then((response) => {
this.zclxList = response
})
.catch((response) => {
console.log(response)
})
},
beforeRouteEnter (to, from, next) {
next(vm => {
vm.$store.dispatch('hideMainTabbar')
})
},
beforeRouteLeave (to, from, next) {
this.$store.dispatch('showMainTabbar')
next()
}
}



