
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'

export default {
components: {
XSwitch,
Group,
XButton,
XHeader,
Cell,
CellBox
},
methods: {
onClick () {
if (window.localStorage.allowImg) {
window.localStorage.removeItem('allowImg')
} else {
window.localStorage.allowImg = true
}
}
},
data () {
return {
showContent001: true,
showContent002: true,
allowImg: window.localStorage.allowImg
}
},
beforeRouteEnter (to, from, next) {
next(vm => {
vm.$store.dispatch('hideMainTabbar')
})
},
beforeRouteLeave (to, from, next) {
this.$store.dispatch('showMainTabbar')
next()
}
}



