
import api from '@/api'
import XHeader from 'vux/src/components/x-header/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
components: {
XHeader,
XImg,
Group,
Cell
},
mounted () {
this.loadUserData()
},
computed: {
deptName () {
let obj = this.userInfo.deptList
let deptNameStr = ''
if (obj) {
for (var i = 0; i < obj.length; i++) {
if (i === (obj.length - 1)) {
deptNameStr += (obj[i].deptName)
} else {
deptNameStr += (obj[i].deptName + '，')
}
}
}
return deptNameStr
},
roleName () {
let obj = this.userInfo.tagList
let roleNameStr = ''
if (obj) {
for (var i = 0; i < obj.length; i++) {
if (i === (obj.length - 1)) {
roleNameStr += (obj[i].tagName)
} else {
roleNameStr += (obj[i].tagName + '，')
}
}
}
return roleNameStr
}
},
data () {
return {
showBack: false,
userInfo: {}
}
},
methods: {
loadUserData () {
let _this = this
api.post('/wx/wxxq/userXQ.do', {appId: _this.$route.query.appId, userId: _this.$route.query.userId}).then(function (res) {
if (res) {
_this.userInfo = res

}
})
}
},
beforeRouteEnter (to, from, next) {
next(vm => {
vm.$store.dispatch('hideMainTabbar')
})
}
}



