
import api from '@/api'
import Pending from '@/components/navi/gz_detail/Pending.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

export default {
components: {
Tab,
TabItem,
Pending
},
props: ['tagName'],
data () {
return {
tabListLv1: ['待处理', '全部'],
wxConfig: {},
tabIndex: 0,
direction: 'forward'
}
},
mounted () {

},
methods: {
getWxConfig () {
let _this = this
api.post('/wx/common/getWXConfig.do', {url: document.URL}).then(function (res) {
if (res) {
_this.wxConfig = res
_this.wxConfig['debug'] = false
_this.wxConfig['jsApiList'] = ['scanQRCode']
}
})
},
scanQrcode () {

let _this = this
let wxObj = this.$wechat
wxObj.config(this.wxConfig)
if (wxObj) {
wxObj.scanQRCode({
desc: '扫描设备二维码',
needResult: 1, // 默认为0，扫描结果由企业微信处理，1则直接返回扫描结果，
scanType: ['qrCode', 'barCode'], // 可以指定扫二维码还是一维码，默认二者都有
success: function (res) {
if (res.resultStr) {
_this.searchParam = res.resultStr
}
},
error: function (res) {
}
})
}
},
onItemClick (index) {
if (index !== this.tabIndex) {
this.tabIndex = index
}
}
}
}



