
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import api from '@/api/index'
export default {
data () {
return {
isVoice: true,
images: {
localId: [],
serverId: []
},
voice: {
localId: '',
serverId: ''
},
config: {}
}
},
computed: {
wx: function () {
let wx = this.$wechat
this.config['debug'] = false
this.config['jsApiList'] = [
'startRecord',
'stopRecord',
'onVoiceRecordEnd',
'playVoice',
'pauseVoice',
'stopVoice',
'onVoicePlayEnd',
'chooseImage',
'uploadVoice',
'downloadVoice',
'previewImage',
'uploadImage',
'downloadImage'
]
wx.config(this.config)
wx.ready(function () {
let _this = this
wx.onVoiceRecordEnd({
complete: function (res) {
_this.voice.localId = res.localId
_this.$vux.alert.show({
title: '',
content: '录音时间已超过一分钟'
})
}
})
})
return wx
}
},
components: {
Grid,
GridItem,
GroupTitle,
XHeader,
Flexbox,
FlexboxItem,
XButton
},
methods: {
cancel () {
this.$router.go(-1)
},
finishRCXJ () {
let _this = this
let ids = _this.images.serverId
let data = ''
for (var i = 0; i < ids.length - 1; i++) {
data = data + ids[i] + '###'
}
data = data + ids[ids.length - 1]
if (ids.length === 0) {
this.$vux.toast.show({
type: 'warn',
text: '请选择图片'
})
return
}
api.post('/wx/gzwx/finishRCXJ.do', {
imgId: data,
voiceId: _this.voice.serverId,
zcId: _this.$route.query.zcId
}).then((response) => {
if (response === 'success') {
this.$vux.toast.show({
text: '成功'
})
this.$router.go(-1)
}
})
.catch((response) => {
_this.$vux.toast.show({
type: 'warn',
text: '失败'
})
})
},
chooseImage () {
let _this = this
_this.wx.chooseImage({
isShowProgressTips: 1, // 默认为1，显示进度提示
success: function (res) {
_this.images.localId = res.localIds
_this.$vux.alert.show({content: '已选择 ' + res.localIds.length + ' 张图片'})
}
})
},
previewImage () {
let _this = this
_this.wx.previewImage({
current: _this.images.localId[0],
urls: _this.images.localId
})
},
uploadImage () {
let _this = this
if (_this.images.localId.length === 0) {
_this.$vux.alert.show({
title: '',
content: '请先使用 chooseImage 接口选择图片'
})
return
}
var i = 0
var length = _this.images.localId.length
_this.images.serverId = []
function upload () {
_this.wx.uploadImage({
localId: _this.images.localId[i],
success: function (res) {
i++
_this.images.serverId.push(res.serverId)
if (i < length) {
upload()
}
},
fail: function (res) {
alert(JSON.stringify(res))
}
})
}

upload()
},
upload () {
},
startRecord () {
this.isVoice = !this.isVoice
let _this = this
this.wx.startRecord({
cancel: function () {
_this.$vux.alert.show({
title: '',
content: '用户拒绝授权录音'
})
}
})
},
stopRecord () {
let _this = this
this.isVoice = !this.isVoice
this.wx.stopRecord({
success: function (res) {
_this.voice.localId = res.localId
},
fail: function (res) {
alert(JSON.stringify(res))
}
})
},
playVoice () {
let _this = this
if (_this.voice.localId === '') {
_this.$vux.alert.show({
title: '',
content: '请先使用 startRecord 接口录制一段声音'
})
return
}
this.wx.playVoice({
localId: _this.voice.localId
})
},
uploadVoice () {
let _this = this
if (_this.voice.localId === '') {
_this.$vux.alert.show({
title: '',
content: '请先使用 startRecord 接口录制一段声音'
})
return
}
_this.wx.uploadVoice({
localId: _this.voice.localId,
success: function (res) {
_this.voice.serverId = res.serverId
}
})
}
},
mounted: function () {
console.log('加载')
api.post('/wx/common/getWXConfig.do', {
url: location.href
}).then((response) => {
this.config = response
})
.catch((response) => {
console.log(response)
})
},
beforeRouteEnter (to, from, next) {
next(vm => {
vm.$store.dispatch('hideMainTabbar')
})
},
beforeRouteLeave (to, from, next) {
this.$store.dispatch('showMainTabbar')
next()
}
}



