
import api from '@/api'
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Card from 'vux/src/components/card/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import Zczt from './Zczt'
import { mapGetters } from 'vuex'
export default {
directives: {
TransferDom
},
components: {
XHeader,
Cell,
Group,
Divider,
Zczt,
Previewer,
Card,
Tabbar,
TabbarItem
},
mounted () {
this.loadZcdata()
},
computed: {
...mapGetters([
'zcxqBackFlag'
]),
gzsj () {
if (this.zcInfo.gzsj) {
return api.getFmtDate(this.zcInfo.gzsj, 'YYYY年MM月DD日')
} else {
return ''
}
},
zjnx () {
if (this.zcInfo.zjnx) {
return this.zcInfo.zjnx + '年'
} else {
return ''
}
},
mainContainer () {
if (this.zcInfo && this.zcInfo.actionList && this.zcInfo.actionList.length > 0) {
return 'mainContainer'
} else {
return ''
}
}
},
data () {
return {
zcInfo: {},
imgList: [],
options: {
getThumbBoundsFn (index) {
let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]
let pageYScroll = window.pageYOffset || document.documentElement.scrollTop
let rect = thumbnail.getBoundingClientRect()
return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}
}
}
}
},
methods: {
loadZcdata () {
let _this = this
let params = {
zcid: _this.$route.query.zcid,
pageUrl: document.URL,
tagName: _this.$route.query.tagName
}
api.post('/wx/zcgl/getZC.do', params).then(function (res) {
if (res) {
_this.zcInfo = res
_this.imgList.push({src: res.picUrl})
}
})
},
show (index) {
this.$refs.previewer.show(index)
},
doAction (action) {
if (action) {
let _this = this
api.post('/wx/zcgl/doAction.do', {zcid: _this.$route.query.zcid, action: action, tagName: _this.$route.query.tagName}).then(function (res) {
if (res) {
let gzCount = res.gzCount
if (gzCount) {
_this.$vux.alert.show({
content: '操作成功',
onHide () {
_this.$router.go(-1)
_this.$store.dispatch('setGzCount', gzCount.toString())
}
})
} else {
_this.$vux.alert.show({
content: '操作失败'
})
}
}
})
}
}
},
beforeRouteEnter (to, from, next) {
if (from.name === 'NaviZc') {
delete to.query.tagName// 我的资产没有操作选项
}
next(vm => {
vm.$store.dispatch('hideMainTabbar')
})
},
beforeRouteLeave (to, from, next) {
this.$store.dispatch('showMainTabbar')
next()
}
}



